import $ from "jquery";

class D3Helpers {
    /**
     * @function getDocumentSize
     * @description gets the document window width/height and document width/height
     */
    getDocumentSize() {
        return {
            window: {
                width: $(window).width(),
                height: $(window).height()
            },
            document: {
                width: $(document).width(),
                height: $(document).height()
            }
        };
    }
}

export default new D3Helpers();